// Colors
$lightblue-text: #7291a5;
$lightblue-bg: #E2EBF3;
$gold: #b07e3e;
$gold-gradient: #C9A576, #F8DEAD; // dark to light
$blue-gradient: linear-gradient(#A8C5DD, #E7F5FF); // dark to light
$black: #000;
$white: #fff;

// Font colors
// $text-base: #333;
// $text-dark: #202020;
// $text-sub: #555;

// Typography
$font-root-size: 16px;
$font-stack: 'proxima-nova', sans-serif;
$header-stack: 'futura-pt', sans-serif;
$style: normal;
$thin: 100;
$light: 300;
$regular: 400;
$medium: 500;
$semibold: 600;
$bold: 700;

// General
html {
    font-size: $font-root-size;
    box-sizing: border-box;
}
*, *::before, *::after {
    box-sizing: inherit;
}
body {
    color: $black;
    font-family: $font-stack;
    font-style: $style;
    font-weight: $regular;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
a,
a:visited,
a:link {
    color: $black;
    text-decoration: none;
    outline: none;
    transition: all .5s ease;
}
a:hover {
    color: $lightblue-text;
    text-decoration: none;
    outline: none;
}
ul,
ol {
    clear: both
}
.fl {
    float: left
}

.fr {
    float: right
}

.fn {
    float: none;
    display: inline-block
}

.tal {
    text-align: left
}

.tar {
    text-align: right
}

.tac {
    text-align: center
}

.clr-l {
    clear: left
}

.clr-r {
    clear: right
}

.clr,
.clear {
    clear: both
}
.nodisp {
    display: none
}
h1,h2,h3,h4,h5,h6 {
    font-family: $header-stack;
}
h1 {
    font-style: $style;
    font-size: 2rem;
    @include bp(md) {
        font-size: 4rem;
    }
}
h2 {
    font-weight: 300;
    font-size: 1.75rem;
    margin-bottom: 1.5rem;
    @include bp(md) {
        font-size: 3rem;
    }
}
h3 {
    color: $lightblue-text;
    margin-bottom: 1rem;
    font-family: $font-stack;
    text-transform: uppercase;
    letter-spacing: 4px;
    font-size: 1.125rem;
}
p {
    padding-bottom: 2rem;
}
p, li {
    line-height: 2rem;
}
ol, ul {
    padding-bottom: 2rem;
}
